body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body,
html {
  font-family: Rubik, sans-serif;
  margin: 0;
  padding: 0;
}

a:visited{
  color:rgb(0, 0, 128);
}

#root {
  margin-bottom: 4em;
}
 
#title,h3 {
  font-weight:normal;
}
header {
  width: 100%;
}
.nav-list li{
  list-style:none;
  display:inline;
}
/* give the source/deployment links cute shapes */
.project-desc a {
  padding: 10px;
  background:#fcb1aa ;
  border-radius: 5px;
  margin: 2px;
  color:rgb(35, 35, 46);
  text-decoration: none;
}

.project-desc p {
  text-align: left;
}

#headshot {
  padding-top:2em;
  height: 225px;
  width:225px; 
}
.header-large {
  padding-bottom: 50px;
  background: rgb(255, 185, 178);
  background: 
    -webkit-radial-gradient(
      top left,
      circle,
      #ffe7e5,
      #fcb1aa 45%
    );
  background: 
    radial-gradient(
      circle at top left,
      #ffe7e5,
      #fcb1aa 45%
    );

  color:rgb(65, 65, 70);
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 85%);
          clip-path: polygon(0 0, 100% 0, 100% 100%, 0 85%);

}
.header-minimized {
  visibility:hidden;
  background: rgb(252, 168, 161);
  height:1em;
}
#header-nav {
  text-decoration: none;
  padding:0;
  list-style: none;
}
#header-nav h1{
  font-size: 3em;
}
#header-nav a {
  text-decoration: none;
  font-size: 2em;
}
#root h1{
  font-weight:normal;
}

#flex-container {
  display:flex;
  flex-direction: column;
  justify-content: center; 
  flex-wrap:wrap;
  align-items: center;
  text-align: center;
}
#base, #contents {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  text-align: center;
}

.subpages-container {
  width: 50%;
  padding: 2em;
  /* border-top: 2px gray solid; */
}
.subpages-container-border-gradient {
  background: -webkit-linear-gradient(left, white,gray, white);
  background: linear-gradient(90deg, white,gray, white);
  height:2px;
}
.section-title{
  font-size: 3em;
}

.project-card {
  /* border: 2px blue solid; */
  border-radius:25px;
  box-shadow: 0px 0px 15px rgb(128, 128, 128);
  padding: 5em;
  width: 30%;
  margin: 2%;
}
footer ul {
  display:inline;
  list-style:none;
}
footer li{
  margin-left: 10px;
  margin-right: 10px;
}

footer {
  position: fixed;
  bottom:0;
  padding: 0.5em;
  background: rgba(255,221,217,0.8);
  width:100%;
}
/* About Me */
.personal-summary li{
  list-style:none;
  
}
.skills-list {
  text-align:left;
}
